}\n\n  &_switch {\n    display: inline-block;\n    width: 32px;\n    padding: 2px;\n    background: rgba(255, 255, 255, 0.3);\n    border-radius: 10px;\n    cursor: pointer;\n    transition: background-color 0.3s cubic-bezier(0.4, 0, 0.2, 1);\n    user-select: none;\n\n    &::after {\n      display: block;\n      width: 16px;\n      height: 16px;\n      background: #fff;\n      border-radius: 50%;\n      transition: transform 0.15s cubic-bezier(0.4, 0, 0.2, 1);\n      content: '';\n    }\n\n    &_active {\n      background: var(--theme-color);\n\n      &::after {\n        transform: translateX(12px);\n      }\n    }\n  }\n\n  &_poster {\n    position: absolute;\n    top: 0;\n    right: 0;\n    bottom: 0;\n    left: 0;\n    background-color: var(--poster-bg);\n\n    &_img {\n      width: 100%;\n      height: 100%;\n      object-fit: contain;\n\n      &_fill {\n        object-fit: fill;\n      }\n    }\n  }\n\n  &_loading {\n    position: absolute;\n    top: 50%;\n    left: 50%;\n    display: block;\n    transform: translate(-50%, -50%);\n\n    &_wave {\n      height: 44px;\n      font-size: 10px;\n      text-align: center;\n\n      & > div {\n        display: inline-block;\n        width: 5px;\n        height: 100%;\n        margin-right: 3px;\n        background-color: var(--spinner-bg);\n        animation: #{$class-prefix}-loading-wave 1.2s infinite ease-in-out;\n\n        @for $i from 2 through 5 {\n          &:nth-child(#{$i}) {\n            animation-delay: -1.3s + ($i * 0.1);\n          }\n        }\n      }\n    }\n\n    &_circle {\n      width: 64px;\n      font-size: 0;\n\n      &_container {\n        position: absolute;\n        top: 50%;\n        left: 50%;\n        width: 100%;\n        margin-top: -50%;\n        margin-left: -50%;\n        padding-bottom: 100%;\n        animation: #{$class-prefix}-loading-circle-linspin 1568.23529647ms linear infinite;\n        pointer-events: none;\n      }\n\n      &_rotator {\n        position: absolute;\n        widt